@import url("https://fonts.googleapis.com/css2?family=Spartan:wght@700&display=swap");@import url("https://fonts.googleapis.com/css2?family=Sanchez&display=swap");.Navbar{color:'white';width:100vw;height:10.5vh;background-color:#232323;display:flex}.Navbar .Navbar-middle{width:40%;height:100;font-family:'Sanchez';color:white;font-size:25px;font-size:29px;display:flex;justify-content:center;align-items:center;font-family:'Spartan';width:21%;min-width:296px;margin:auto 3%;letter-spacing:-1px;cursor:default;height:100%}.Navbar .Menu{width:40%;height:100;font-family:'Sanchez';color:white;font-size:25px;display:flex;justify-content:center;align-items:center}.Navbar .right{justify-content:start}.Navbar .left{justify-content:end}.Navbar .Navbar-button{display:flex;justify-content:center;align-items:center;color:#fff;height:9.5vh;margin-left:1.7vw;margin-right:1.7vw;letter-spacing:.7px;cursor:pointer;text-decoration:none;position:relative;width:13%}.Navbar a{text-decoration:none}.notallowed{cursor:wait !important}.Navbar-button:before{content:"";position:absolute;width:115%;height:2px;bottom:8px;background-color:rgba(255,255,255,0.8);opacity:1;transform:scale(0);transition:all .2s ease-in-out 0s;z-index:10}.Navbar-button:hover:before{opacity:1;left:0;transform:scaleX(1)}
